<template>
  <div class="wrapper">
    <img src="~images/share-icon.png" class="share-icon" alt="">
    <div class="qr-box">
      <img :src="qr" class="qr" alt="">
    </div>
    <div class="qr-tip">{{$t('share.tip')}}</div>
    <div class="qr-link">{{link}}</div>
    <div class="cp-btn clip-button" :data-clipboard-text="link">{{$t('share.copy')}}</div>
  </div>
</template>

<script>
import Clipboard from 'clipboard'
import QRCode from 'qrcode'
import { commonMixin } from '@/utils/mixins'

export default {
  mixins: [ commonMixin ],
  data () {
    return {
      qr: ''
    }
  },
  computed: {
    link () {
      return `https://oxquizroom-xs.dsceshi.cn/#/?inviteAddress=${this.address}`
    }
  },
  created () {
    if (this.address) {
      this.genQr()
    }
  },
  methods: {
    onAddress () {
      this.genQr()
    },
    genQr () {
      QRCode.toDataURL(this.link, (err, url) => {
        this.qr = url
      })
      let clipboard = new Clipboard('.clip-button')
      clipboard.on('success', e => {
        e.clearSelection()
        this.$toast(this.$t('tip.cs'))
      })
      clipboard.on('error', () => {
        this.$toast.fail(this.$t('tip.cf'))
      })
    }
  }
}
</script>

